.container{
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: linear-gradient(#121212, #212b46);
}

.title{
    font-size: 60px;
    color: #FFF;
    animation: flipTitle 1.8s;

}
@keyframes flipTitle {
    from{
        transform: rotateX(90deg);
    }
    to{
        transform: rotateX(0deg);

    }
    
}
.containerInput{
    background-color: rgba(255, 255, 255, 0.2);
    padding: 15px;
    margin: 35px 0;
    display: flex;
    border: 8px;
    box-shadow: 1px 3px 8px rgba(0, 0, 0, 0.5);
}

.container input{
    background-color: transparent;
    border: none;
    font-size: 20px;
    color: #FFF;
    outline: none;
    margin-right: 10px;
}
.container input::placeholder{
    color: #f1f1f1bb;
}

.buttonBusca{
    background-color: transparent;
    border: none;
    display: flex;
    justify-content: center;
    cursor: pointer;
    transition: tranform 0.5s;
}

.buttonBusca:hover{
    transform: scale(1.2);
}

.main{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: #FFF;
    width: 500px;
    border-radius: 8px;
}

.main h2{
    margin: 16px 0;
    font: 39px;
}

.main span{
    margin-bottom: 16px;
    font-weight: bold;
}

@media (max-width: 620px) {
    .title{
        font-size: 40px;
    }
    .main h2{
        font-size: 28px;
    }
    .main{
        width: 80%;
        min-height: 250px;
    }
}